html {
  width: 100%;
  box-sizing: border-box;
  fot-size: 62.5%;
  background-color: #1d1d1d;
}
.App {
  text-align: center;
}
header {
  background-color: #3bd665;
  width: 100%;
}
.header_title {
  font-size: 4rem;
  font-family: bungee, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #1d1d1d;
  height: 100px;
  padding-top: 12px;
  min-width: 750px;
}

/* ------------ HottestMusic ---------------- */

.HottestMusic {
  background-color: #272727;
  width: 80%;
  height: 600px;
  max-width: 1400px;
  min-width: 750px;
  margin: 50px auto 70px;
  padding-top: 15px;
  border-radius: 30px;
  box-shadow: 8px 10px 30px;
}
.HottestMusic h2 {
  color: #fff;
  background-color: #3bd665;
  font-family: bungee, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 3rem;
  padding-bottom: 8px;
  border-bottom: solid 5px #fff;
}
